import { Component } from '@angular/core';
import {StoryService} from './storyService'
import { Observable } from 'rxjs/Observable';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
 
})
export class AppComponent {
  private defects=null;
  constructor(private _storyService:StoryService){

  }
ngOnInit()
{
  this.getDefects();
  
}  


getDefects() {
  this.defects = this._storyService.returnDefects().subscribe(
    // the first argument is a function which runs on success
    data => { this.defects = data},
    
    // the second argument is a function which runs on error
    err => console.error(err),
    // the third argument is a function which runs on completion
    () => console.log('done loading defects')
  );
}

updateDefect(defect) {
  this._storyService.updateDefect(defect).subscribe(
     data => {
       // refresh the list
       this.getDefects();
       return true;
     },
     error => {
       console.error("Error saving defect!");
       return Observable.throw(error);
     }
  );
}

deleteDefect(defect) {
  if (confirm("Are you sure you want to delete " + defect.story + "?")) {
    this._storyService.deleteDefect(defect.idn).subscribe(
       data => {
         // refresh the list
         this.getDefects();
         return true;
       },
       error => {
         console.error("Error deleting defect!");
         return Observable.throw(error);
       }
    );
  }
}

addRow() {
  let defect = {idn:"",story:"new",developer:"" ,tester:"", analysis:true ,coding:false ,testing:false ,done:false};
  this._storyService.createDefect(defect).subscribe(
     data => {
       // refresh the list
       this.getDefects();
       return true;
     },
     error => {
       console.error("Error saving defect!");
       return Observable.throw(error);
     }
  );
}

}



