import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { fail } from 'assert';



@Component({
  selector: 'story',
  templateUrl: './story.component.html',
  styleUrls: ['./story.component.css'],
  
})
export class StoryComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }
  @Input()
  public defects : any ;
  @Output()
  public delete = new EventEmitter<any>();
  @Output()
  public update = new EventEmitter<any>();

  selectAnalysis(defect){
    defect.analysis=true;
    defect.coding=false;
    defect.done = false;
    defect.testing=false;
    // this.update.emit({defect:defect});
  };
  selectCoding(defect){
    defect.analysis=false;
    defect.coding=true;
    defect.done = false;
    defect.testing=false;
    // this.update.emit({defect:defect});
  };  selectTesting(defect){
    defect.analysis=false;
    defect.coding=false;
    defect.done = false;
    defect.testing=true;
    // this.update.emit({defect:defect});
  };  selectDone(defect){
    defect.analysis=false;
    defect.coding=false;
    defect.done = true;
    defect.testing=false;
    // this.update.emit({defect:defect});
  }; 

  deleteMe(defect){
  this.delete.emit({defect:defect});
  };
  updateMe(defect){
    this.update.emit({defect:defect});
    };
}



