import {Injectable} from '@angular/core'
import { Http, RequestOptions, Headers } from "@angular/http";
import 'rxjs/add/operator/map'

@Injectable()
export class StoryService{
    
    constructor(private http: Http) {
}
    private defects =null;

    returnDefects() {
        return this.http.get('/defects').map(res => res.json());
      }

     deleteDefect(index){
        return this.http.delete('/defect/' + index);
        // for (var i = 0, len = this.defects.length; i < len; i++) {
        //     if(this.defects[i].idn == index){
        //         this.defects.splice(i,1);
        //     }
        //   }
      }
      updateDefect(defect){
        let headers = new Headers({'Content-Type': 'application/json'});
        let options = new RequestOptions({headers: headers});
        let body = JSON.stringify({"idn": defect.idn, "story": defect.story, "developer": defect.developer, "tester": defect.tester, "analysis": defect.analysis, "coding": defect.coding, "testing": defect.testing, "done": defect.done});

        return this.http.put('/defect/' + defect.idn, body, options).map((res) => res.json());

        // for (var i = 0, len = this.defects.length; i < len; i++) {
        //     if(this.defects[i].idn ==defect.idn){
        //         this.defects[i]=defect;
               
        //     }
        //   }
      }
      createDefect(defect) {
        let headers = new Headers({'Content-Type': 'application/json'});
        let options = new RequestOptions({headers: headers});
        let body = JSON.stringify({"idn": defect.idn, "story": defect.story, "developer": defect.developer, "tester": defect.tester, "analysis": defect.analysis, "coding": defect.coding, "testing": defect.testing, "done": defect.done});
        return this.http.post('/defect/', body, options).map((res) => res.json());
    }

 }


